import * as uniqid from "uniqid";
import { updateServingsIngredients } from "../views/recipeView";
import { IIngredientObj } from "./Recipe";

export default class List {
  public items: IIngredientObj[];
  constructor() {
    this.items = [];
  }

  public addItem(count: number, unit: string, ingredient: string) {
    const item: IIngredientObj = {
      count,
      id: uniqid(),
      ingredient,
      unit,
    };
    this.items.push(item);
  }

  public deleteItem(id: string) {
    const index = this.items.findIndex((el) => el.id === id);
    this.items.splice(index, 1);
  }

  public updateCount(id: string, newCount: number) {
    this.items.find((el) => el.id === id).count = newCount;
  }
}
